@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

* {
    box-sizing: border-box;
}

body {
    background-color: #111;
    color: #FFF;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.lists {
    margin-top: -130px;
}

footer {
    margin: 50px 0;
    text-align: center;
}

.loanding {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background-color: #0000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loanding img {
    height: 50vw;
}